import React, { Component, PropTypes } from 'react'

import Video from '../Video'
import IntroCard from '../IntroCard'
import Timeline from '../Timeline'
import TutitionFree from '../TutitionFree'

import './about.scss'

export default class About extends Component {
	render() {
		return (
			<div className="about__content">
				<div className="wrapper-1000px wrapper-padding-top wrapper-padding-bottom">
			 		<p className="gray-text gray-text_bold">
			 			Mission: To provide Chinas finest young musicians access to superior performance and training opportunities and to allow them to serve as ambassadors for classical music and their country on a world stage.
			 		</p>
			  		<p className="gray-text">
				  		The first annual National Youth Orchestra of China is proud to present an international concert tour of the United States and China during the summer of 2017, inaugurated by a gala performance in Carnegie Hall. For two weeks in July, approximately one hundred high school-aged musicians will gather on a university campus in the Northeast United States to participate in W Conservatory  NYO-Chinas intensive orchestral residency program - under the direction of a world-class teaching faculty and a renowned guest conductor, Maestro Ludovic Morlot. After two weeks of preparation, the orchestra will debut in New York City and thereafter embark on a three-city concert tour of China featuring a soloist who will resonate with Chinese and Western audiences alike.
				  		<br/><br/>
				  		Inspired by Carnegie Halls own National Youth Orchestra of the United States of America (NYO-USA), NYO-China is meant to provide Chinese musicians access to the world-class education and performance model developed by its American counterpart. Members will have the chance to train under some of the finest living classical musicians and then perform as an orchestra on a world stage. After applying through a competitive virtual audition process, accepted members will be given the chance to serve as ambassadors for both classical music and their country. Moreover, to ensure that no superior musician will ever be turned away for lack of funds, the entire program  including housing, tuition, and travel  will be free of charge.
			  		</p>
					{/*
					<div className="wrapper-padding-top">
						<Video title="The Imperial March by the Vienna Philharmonic Orchestra">
							<source src="video/vienna.mp4" type="video/mp4"/>
						</Video>
					</div>
					*/}
				</div>
				<hr/>		 	
				<div className="wrapper-1000px">
					<div className="wrapper-padding-top wrapper-padding-bottom">
						<IntroCard
							image="img/about-w-conservatory.jpg"
							title="W CONSERVATORY TRAINING RESIDENCY"
							description="W Conservatory, the training residency of NYO-China, is designed to develop individual members skills as musicians and to prepare the orchestra as a whole for its concert tour. Members will participate in a two-week training program, consisting of sectional rehearsals led by a world-class faculty of coaches and full orchestra rehearsals under the direction NYO-Chinas music director and guest conductor. Members will also have access to other learning and exchange opportunities such as master classes, recreational chamber music readings, weekend concert excursions, and social interactions with NYO-USA."
							align="left"/>
						<IntroCard
							image="img/about-concert-tour.jpg"
							title="CONCERT TOUR OF CHINA"
							description="NYO-Chinas concert tour will last for one week immediately following its debut performance in Carnegie Hall on July 22nd. Upon arrival in China, the orchestra will perform in premier venues throughout the country, including the National Centre for the Performing Arts (NCPA) in Beijing, the Shanghai Grand Theatre, and the Guangzhou Opera House. This tour will showcase the orchestra on an international level and allow NYO-Chinas musicians to expand the reach of this remarkable cultural exchange to audiences at home."
							align="right"/>
					</div>
					<hr className="short"/>
					<Timeline
						auditionProcessData={this.props.auditionProcessData}
						concertTourData={this.props.concertTourData}/>
				</div>
				<hr/>
				<div className="wrapper-1000px wrapper-padding-top wrapper-padding-bottom">
					<TutitionFree/>
				</div>
			</div>
		)
	}
}

About.propTypes = {
	auditionProcessData: PropTypes.array.isRequired,
	concertTourData: PropTypes.array.isRequired,
}


