import React from 'react'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'
import cx from 'classnames'
import './image-grid.scss'

export default class ImageGrid extends React.Component {
	constructor(props) {
		super(props)

		this.state = {
			selectedIndex: -1
		}
		this.handleClick = this.handleClick.bind(this)
		this.handleClose = this.handleClose.bind(this)
	}
	handleClick(index) {
		var newIndex = this.state.selectedIndex === index ? -1 : index;


		this.setState({
			selectedIndex: newIndex
		})
	}
	handleClose() {
		this.setState({
			selectedIndex: -1
		})
	}
	render() {
		// generate grid items from props
		var gridItems = this.props.images.map((item, index) => {
			return (
				<ImageGridItem
					image={item.image}
					title={item.title}
					text={item.text}
					onClick={this.handleClick.bind(this,index)}
					onClose={this.handleClose.bind(this)}
					active={index === this.state.selectedIndex}
					key={index}
					index={index}
				/>
			)
		})
		var className = cx('image-grid', {'image-grid_has-item': this.state.selectedIndex !== -1});

		return (
			<div className="image-grid-container">
				{this.state.selectedIndex !== -1 && <div className="opacity opacity_mobile-only" onClick={this.handleClose}></div>}
				<div className={className}>{gridItems}</div>
				
			</div>
		)
	}
}

class ImageGridItem extends React.Component {
	constructor(props) {
		super(props)

		this.state = {
			isHovered: false,
			isScaled: false,
			hasBackgroundTransition: true,
			scale: 1,
			translateX: 0,
			translateY: 0
		}

		this.handleClick = this.handleClick.bind(this)
		this.handleClose = this.handleClose.bind(this)
		this.handleMouseMove = this.handleMouseMove.bind(this)
		this.handleMouseEnter = this.handleMouseEnter.bind(this)
		this.handleMouseLeave = this.handleMouseLeave.bind(this)
	}
	handleClick() {
		!this.props.active && this.props.onClick && this.props.onClick()
	}
	handleClose() {
		this.props.onClose && this.props.onClose()
	}
	handleMouseMove(event) { 
		if (this.props.active) return;
		var {width, height} = mouseTarget(event).getBoundingClientRect()
		var {x, y} = mousePositionElement(event)
		// calculate real x for translated elements
		var translateX = +event.target.getAttribute('data-translate-x')
		x -= (translateX / 100) * width

		const maxParallaxOffsetX = 19.75
		const maxParallaxOffsetY = 8

		var halfWidth = width / 2,
			halfHeight = height / 2,
			offsetX = halfWidth - x,
			offsetY = halfHeight - y,
			offsetPercentsX = offsetX / halfWidth,
			offsetPercentsY = offsetY / halfHeight;

		this.setState({
			translateX: - (maxParallaxOffsetX * offsetPercentsX),
			translateY: - (maxParallaxOffsetY * offsetPercentsY)
		})
		
	}
	handleMouseEnter() {
		this.setState({
			isScaled: true,
			scale: 1.12,
			isHovered: true
		}, () => {
			setTimeout(() => {
				this.setState({hasBackgroundTransition: false})
			}, 200)
		})
		
	}
	handleMouseLeave() {
		this.setState({
			isHovered: false,
			isScaled: false,
			hasBackgroundTransition: true,
			scale: 1,
			translateX: 0,
			translateY: 0,
		})
	}
	render() {
		// absolute position
		const perRow = 3;
		const itemSize = 32.6;
		const itemPadding = 3.5;

		let index = this.props.index;
		let itemPositionStyle = {}
		let dataTranslateX = 0

		if (index < 3) {
			itemPositionStyle.top = 0
		} else {
			itemPositionStyle.top = '51.6%';
		}

		if (index % perRow == 0) {
			itemPositionStyle.left = 0;
			itemPositionStyle.transform = 'translate3d(0, 0, 0)'
			dataTranslateX = 0
		} else if (index % perRow == 1) {
			itemPositionStyle.left = '50%';
			itemPositionStyle.transform = 'translate3d(-50%, 0, 0)'
			dataTranslateX = -50
		} else if (index % perRow == 2) {
			itemPositionStyle.left = '100%';
			itemPositionStyle.transform = 'translate3d(-100%, 0, 0)'
			dataTranslateX = -100
		}


		var classes = cx('image-grid-item', {'image-grid-item_active': this.props.active, 'image-grid-item_hovered': this.state.isScaled || this.state.isHovered});
		var backgroundStyle = {
			backgroundImage: 'url(' + this.props.image + ')'
		};
		var transforms = [];

		if (this.state.isHovered && !this.props.active && !('ontouchstart' in document.documentElement)) {
			var {translateX, translateY} = this.state;
			transforms.push('translate3d(' + translateX + 'px, ' + translateY + 'px, 0)')
		}

		if (!this.state.hasBackgroundTransition) {
			backgroundStyle.transition = 'none';
		}

		if (this.state.isScaled && !this.props.active) {
			var scale = this.state.scale;
			transforms.push('scale3d('+[scale, scale, scale].join(', ')+')')
		}

		backgroundStyle.transform = transforms.join(' ')

		return (
			<div className={classes} data-translate-x={dataTranslateX} onClick={this.handleClick.bind(this)} onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave} onMouseMove={this.handleMouseMove} style={itemPositionStyle}>
				<div className="image-grid-item__background" style={backgroundStyle}></div>
				<div className="image-grid-item__title">{this.props.title}</div>
				{this.props.active && <div className="image-grid-item__close" onClick={this.handleClose}><i className="icon icon-close"></i></div>}
				<div className="image-grid-item__text">{this.props.text}</div>
			</div>
		)
	}
}

// Which HTML element is the target of the event
function mouseTarget(e) {
	var targ;
	if (!e) var e = window.event;
	if (e.target) targ = e.target;
	else if (e.srcElement) targ = e.srcElement;
	if (targ.nodeType == 3) // defeat Safari bug
		targ = targ.parentNode;
	return targ;
}
 
// Mouse position relative to the document
// From http://www.quirksmode.org/js/events_properties.html
function mousePositionDocument(e) {
	var posx = 0;
	var posy = 0;
	if (!e) {
		var e = window.event;
	}
	if (e.pageX || e.pageY) {
		posx = e.pageX;
		posy = e.pageY;
	}
	else if (e.clientX || e.clientY) {
		posx = e.clientX + document.body.scrollLeft + document.documentElement.scrollLeft;
		posy = e.clientY + document.body.scrollTop + document.documentElement.scrollTop;
	}
	return {
		x : posx,
		y : posy
	};
}

// Find out where an element is on the page
// From http://www.quirksmode.org/js/findpos.html
function findPos(obj) {
	var curleft = 0,
		curtop = 0;
	if (obj.offsetParent) {
		do {
			curleft += obj.offsetLeft;
			curtop += obj.offsetTop;
		} while (obj = obj.offsetParent);
	}
	return {
		left : curleft,
		top : curtop
	};
}
 
// Mouse position relative to the element
// not working on IE7 and below
function mousePositionElement(e) {
	var mousePosDoc = mousePositionDocument(e);
	var target = mouseTarget(e);
	var targetPos = findPos(target);
	var posx = mousePosDoc.x - targetPos.left;
	var posy = mousePosDoc.y - targetPos.top;
	return {
		x : posx,
		y : posy
	};
}


