import React from 'react'
import cx from 'classnames'
import { Link } from 'react-router'

import './footer.scss'

export default class extends React.Component {
	shouldComponentUpdate() {
		return false;
	}
	render() {
		var className = cx('footer', {'footer_gray': this.props.gray})
		return (
			<footer className={className}>
				<div className="wrapper-1000px">
					<div className="footer__blocks">
						<div className="footer-block">
							<div className="footer-block__title">
								<a href="http://www.athenaeg.com" target="_blank">
									<span className="footer-block__external-link-text">Athena Education Group</span><i className="icon icon-external-link"></i>
								</a>
							</div>
							<div className="footer-block__text">
								Based in Midtown Manhattan, Athena Education Group is dedicated to delivering superior international learning solutions for students in the United States and China. Athena pursues a rigorous understanding of the strengths and challenges of cross-cultural learning opportunities to create services that are both meaningful and relevant in the 21st century.
							</div>
						</div>
						<div className="footer-block">
							<div className="footer-block__title">Links</div>
							<ul className="footer-block__menu">
								<li className="footer-block__menu-item">
									Leadership
									{/*<Link to="/leadership">Leadership</Link>*/}
								</li>
								<li className="footer-block__menu-item">
									Sponsors
									{/*<Link to="/sponsors">Sponsors</Link>*/}
								</li>
								<li className="footer-block__menu-item">
									Newsroom
									{/*<Link to="/newsroom">Newsroom</Link>*/}
								</li>
								<li className="footer-block__menu-item">
									Privacy
									{/*<Link to="/privacy">Privacy</Link>*/}
								</li>
								<li className="footer-block__menu-item">
									Legal
									{/*<Link to="/legal">Legal</Link>*/}
								</li>
							</ul>
						</div>
					</div>
				</div>
				<div className="footer__copyright">Copyright &copy; Athena Education Group. All rights reserved.</div>
			</footer>
		)
	}
}


