import React from 'react'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'
import cx from 'classnames'

import './timeline-item.scss'
import './tooltip.scss'

export var TimelineItem = React.createClass({
  	getInitialState: function() {
	  	return {
	  		isTooltipOpen: false
	  	}
	},
	onIconClick: function() {
		if ('ontouchstart' in document.documentElement) {
			this.setState({isTooltipOpen: !this.state.isTooltipOpen})
		}
	},
	onMouseEnter: function () {
		if ('ontouchstart' in document.documentElement) return
		this.setState({isTooltipOpen: true});
	},
	onMouseLeave: function () {
		if ('ontouchstart' in document.documentElement) return
		this.setState({isTooltipOpen: false});
	},
  	render: function() {
  		var classes = cx('timeline__block', {'timeline__block_active': this.state.isTooltipOpen});
  		if (this.props.align) {
  			classes += ' timeline__block_' + this.props.align
  		}
  		var fontIconClasses = 'icon icon-'+ this.props.icon;
  		var iconClasses = cx('timeline-item__icon', {'timeline-item__icon_active': this.state.isTooltipOpen});

		return (
			<div className={classes}>
				<div className="timeline-item">
					<div className="timeline-item__event">{this.props.title}</div>
					<div className={iconClasses} onMouseEnter={this.onMouseEnter} onMouseLeave={this.onMouseLeave} onClick={this.onIconClick}>
				  		<i className={fontIconClasses}></i>
					</div>
					<div className="timeline-item__date">{this.props.date}</div>
					<ReactCSSTransitionGroup transitionName="tooltip-animation" transitionEnterTimeout={150} transitionLeaveTimeout={150}>
						{this.state.isTooltipOpen && this.props.children}
					</ReactCSSTransitionGroup>
				</div>
		  	</div>
		)
  	}
})

export var Tooltip = React.createClass({
	componentDidMount: function() {
		if (this.props.align) {
			var parentWidth = this.refs.tooltip.parentNode.parentNode.clientWidth,
				arrowOffset = (parentWidth / 2 - 10) + 'px'; // 10 is arrow width
			if (this.props.align === 'left') {
				this.refs.arrow.style.right = 'auto';
				this.refs.arrow.style.left = arrowOffset;
			} else if (this.props.align === 'right') {
				this.refs.arrow.style.left = 'auto';
				this.refs.arrow.style.right = arrowOffset;
			}
		}
	},
	render: function () {
		var classes = 'tooltip' + (this.props.align ? ' tooltip_' + this.props.align : '');
		return (
			 <div className={classes} ref="tooltip">
			 	<span className="tooltip__arrow" ref="arrow"></span>
			 	<div className="tooltip__content">{this.props.children}</div>
			 </div>
		)
	}
});


