import React from 'react'
import cx from 'classnames'

import './intro-card.scss'

export default class IntroCard extends React.Component {
	render() {
		let align = this.props.align,
			imageClassName = cx('intro-card__image', `intro-card__image_${align}`),
			textClassName = cx('intro-card__text', `intro-card__text_${align}`)

		return (
			<div className="intro-card">
				<img className={imageClassName} src={this.props.image}/>
				<div className={textClassName}>
					<div className="intro-card__content">
						<div className="intro-card__title">{this.props.title}</div>
						<img className={imageClassName} src={this.props.image}/>
					</div>
					<div className="intro-card__subtitle">{this.props.subtitle}</div>
					<div className="intro-card__description"><p className="gray-text">{this.props.description}</p></div>
				</div>
			</div>
		)
	}
}

IntroCard.propTypes = { align: React.PropTypes.oneOf(['left', 'right']) }
IntroCard.defaultProps = { align: 'left' }


