import React from 'react'
import ReactDOM from 'react-dom';

import classNames from 'classnames'

import Navigation from '../../components/Navigation'
import Hero from '../../components/Hero'
import IntroSliders from '../../components/IntroSliders'
import ImageTitle from '../../components/ImageTitle'
import About from '../../components/About/'
import HeadliningArtists from '../../components/HeadliningArtists/'
import WConservatory from '../../components/WConservatory/'
import ConcertTour from '../../components/ConcertTour/'
import Auditions from '../../components/Auditions/'
import Contact from '../../components/Contact'
import Footer from '../../components/Footer'

export default class extends React.Component {
  	render() {
	  	// from data.json
		var data = this.props.data;
	    return (
	    	<div className="app">
	    		<Navigation />
	    		<Hero/>

				<IntroSliders
					endorsements={data.endorsements}
					sponsors={data.sponsors}/>

				<ImageTitle image="img/about.jpg">About</ImageTitle>
				<About
					auditionProcessData={data.audition_process_data}
					concertTourData={data.concert_tour_data}/>

				<ImageTitle image="img/headlining-artists.jpg">Headlining Artists</ImageTitle>
				<HeadliningArtists artists={data.headlining_artists}/>

				<ImageTitle image="img/w-conservatory.jpg">W Conservatory</ImageTitle>
				<WConservatory
					images={data.w_conservatory.image_grid}
					faculty={data.w_conservatory.faculty}
					auditions={data.auditions}/>

				<ImageTitle image="img/concert-tours.jpg">Concert Tour</ImageTitle>
				<ConcertTour data={data.concert_tour_data}/>

				<ImageTitle image="img/auditions.jpg">Auditions</ImageTitle>
				<Auditions auditionsRequirements={data.auditions}/>

				<Contact/>

				<Footer/>
			</div>
	    )
  	}
}


